<template>
  <!--商品详情-->
  <div id="orderDetail" class="left">
    <home-header :title="info?info.productName:'商品详情'" :back="true"></home-header>
    <main class="detail-main" v-if="info">
      <div class="detail-main-image">
        <mt-swipe :auto="0">
          <mt-swipe-item v-for="(item,index) in info.stringArray" :key="index" v-if="item">
            <img :src="imgHost+item">
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="detail-main-title">
        <div class="detail-title">
          {{info.productName}}
        </div>
        <div class="detail-collect" v-if="false">
          <i class="iconfont" :class="{active:iconfontActive}">

          </i>
          收藏
        </div>
      </div>
      <div class="detail-main-money">
        ￥{{info.discountPrice}}元
      </div>
      <div class="detail-main-mon">
        市场价<span>￥{{info.salesPrice}}元</span>
      </div>
      <div class="detail-main-sales">
        <span>销量：{{info.salesVolume}}</span>
        <span>库存：{{info.stock}}</span>
      </div>
      <div class="detail-main-tag clear">
        <div class="detail-notice">
          本服务由商户提供售后服务与产品支持
        </div>
        <li class="detail-tag-list" v-for="(item,index) in tagList" :key="index">
          <div class="detail-tag-li">
            <i class="iconfont" :class="item.iconfont"></i>{{item.name}}
          </div>
        </li>
      </div>
      <div class="detail-main-sel" @click="popupVisible=true">
        <div class="detail-sel-left">已选</div>
      </div>
      <div class="detail-main-sel">
        <div class="detail-sel-left">用户评价</div>
        <div class="detail-sel-evaluate">好评率 <span> 100%</span></div>
      </div>
      <div class="detail-main-intro">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1">商品描述</mt-tab-item>
          <mt-tab-item id="2">规格参数</mt-tab-item>
        </mt-navbar>

        <!-- tab-container -->
        <mt-tab-container v-model="selected">
          <mt-tab-container-item id="1">
            <div class="html-detail" v-html="info.remark">

            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="html-detail" v-html="info.remark">

            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </main>
    <mt-popup v-model="popupVisible" position="bottom">
      <div class="sel-info" v-if="info">
        <div class="sel-info-title">
          <div class="sel-info-image">
            <img src="http://temp.im/360x168/4CD964/fff" alt="">
          </div>
          <div class="sel-info-item">
            <div class="sel-info-name">
              {{info.productName}}<i class="iconfont icon-guanbi" @click="popupVisible = false"></i>
            </div>
            <div class="sel-info-money">
              ￥{{info.discountPrice}}元
            </div>
            <div class="sel-info-inventory">
              库存：{{info.stock}}
            </div>
          </div>
        </div>
        <div class="sel-info-sel">
          <!--保留-->
          <!--<div class="sel-info-select" v-for="(item,index) in selList" :key="index">-->
          <!--<div class="sel-title">-->
          <!--{{item.name}}：-->
          <!--</div>-->
          <!--<div class="sel-list clear">-->
          <!--<div class="sel-li" v-for="(item,index2) in item.selLi" :key="index2"-->
          <!--@click="active.splice(index,1,index2)"-->
          <!--:class="index2== active[index]?'activeClass':''">{{item}}-->
          <!--</div>-->
          <!--</div>-->
          <!--</div>-->
          <div class="sel-info-select" v-if="info.color">
            <div class="sel-title">
              颜色：
            </div>
            <div class="sel-list clear">
              <div class="sel-li" v-for="(item,index2) in colorList(info.color)" :key="index2"
                   @click="active.splice(0,1,index2)"
                   :class="index2== active[0]?'activeClass':''">{{item}}
              </div>
            </div>
          </div>
          <div class="sel-info-select" v-if="info.size">
            <div class="sel-title">
              颜色：
            </div>
            <div class="sel-list clear">
              <div class="sel-li" v-for="(item,index2) in colorList(info.size)" :key="index2"
                   @click="active.splice(1,1,index2)"
                   :class="index2== active[1]?'activeClass':''">{{item}}
              </div>
            </div>
          </div>
          <div class="sel-info-count">
            <div class="sel-count-title">
              数量：
            </div>
            <div class="sel-minus-add">
              <div class="sel-minus" @click="minusCount">
                -
              </div>
              <div class="sel-count">
                {{orderInfo.count}}
              </div>
              <div class="sel-add" @click="addCount">
                +
              </div>
            </div>
          </div>
        </div>
        <div class="sel-info-button">
          <div class="sel-info-btn sel-info-addShop" @click="addShop">加入购物车</div>
          <div class="sel-info-btn sel-info-buy" @click="buyShop">立即购买</div>
        </div>
      </div>
    </mt-popup>
    <div class="orderDetail-button">
      <div class="orderDetail-shopcart" @click="hrefName('shopcart')">
        <i class="iconfont icon-gouwuche2"></i>
        购物车
        <span class="superscript">{{shopCount}}</span>
      </div>
      <div class="orderDetail-btn sel-info-addShop" @click="popupVisible=true">加入购物车</div>
      <div class="orderDetail-btn sel-info-buy" @click="popupVisible=true">立即购买</div>

    </div>
  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'
  import {Toast} from 'mint-ui'

  export default {
    data() {
      return {
        popupVisible: false,
        selected: '1',
        info: null,
        shopCount: 0,
        typeSel: null,
        proId: null,
        orderInfo: {
          count: 1,
        },
//        保留
//        selList: [
//          {
//            name: '尺寸',
//            selLi: ['S', 'M'],
//          },
//          {
//            name: '颜色',
//            selLi: ['红色', '黄色'],
//          }
//        ],
        selList: ['color', 'size'],
        tagList: [
          {
            name: '正品保证',
            iconfont: 'icon-zhengpinbaozhang',
          },
          {
            name: '货到付款',
            iconfont: 'icon-weibiaoti-'
          },
          {
            name: '7天退换',
            iconfont: 'icon-7tiantuihuan',
          },
          {
            name: '极速达',
            iconfont: 'icon-shandianfahuo',
          }],
        active: [0, 0, 0, 0],
        iconfontActive: false,
      }
    },
    computed: {},
    components: {
      homeHeader,
    },
    methods: {
      colorList(val) {
        return val.split('|');
      },
      addShop() {
        let that = this;
        that.typeJson();
        that.localProId();
        that.ajax({
          url: '/cart/add',
          method: 'post',
          data: {
            productId: that.proId,
            count: that.orderInfo.count,
            productText: that.typeSel,
          },
          success: function (res) {
            if (res.data.cartProductDtoList[0].userId) {
//              console.log(res.data.cartProductDtoList[0].userId)
              window.sessionStorage.setItem('tempMemberId', res.data.cartProductDtoList[0].userId);
            }
            that.getShopCount();
            that.popupVisible = false;
            Toast("添加购物车成功")
          }
        })
      },
      typeJson() {
//        let that = this;
//        let typeSel = '[';
//        for (var i = 0, l = that.selList.length; i < l; i++) {
//          typeSel += '{"' + that.selList[i].name + '":';
//          typeSel += '"' + that.selList[i].selLi[that.active[i]] + '"}';
//          if (i < l - 1) {
//            typeSel += ',';
//          }
//        }
//        typeSel += ']';
        let that = this;
        let typeSel = '[';
        for (var i = 0, l = that.selList.length; i < l; i++) {
          let arr = that.info[that.selList[i]].split('|');
          typeSel += '{"' + that.selList[i] + '":';
          typeSel += '"' + arr[that.active[i]] + '"}';
          if (i < l - 1) {
            typeSel += ',';
          }
        }
        typeSel += ']';
        that.typeSel = typeSel;
        console.log(that.typeSel);
      },
      buyShop() {
        let that = this;
        that.localProId();
        that.typeJson();
        window.localStorage.setItem("count", that.orderInfo.count)
        window.localStorage.setItem("typeSel", that.typeSel)
        window.localStorage.setItem("proId", that.proId)
        that.$router.push({
          name: 'createOrder',
          query: {proId: that.proId, count: that.orderInfo.count, typeSel: that.typeSel}
        })
      },
      minusCount() {
        if (this.orderInfo.count > 1) {
          this.orderInfo.count--;
        }
      },
      addCount() {
        if (this.orderInfo.count < 100) {
          this.orderInfo.count++;
        }
      },
      localProId() {
        let that = this;
        let proId = that.$route.query.proId;
        if (proId) {
          window.localStorage.setItem('proId', proId);
        } else {
          proId = window.localStorage.getItem('proId');
        }
        that.proId = proId;
      },
      getInfo() {
        let that = this;
        let proId = that.$route.query.proId;
        that.localProId();
        that.ajax({
          url: '/product/detail2?id=' + that.proId,
          success: function (res) {
            that.info = res.data;
          }
        })
      },
      getShopCount() {
        let that = this;
        let memberId = null;
        if (window.sessionStorage.getItem('tempMemberId')) {
          memberId = window.sessionStorage.getItem('tempMemberId');
        } else if (window.localStorage.getItem('memberId')) {
          memberId = window.localStorage.getItem('memberId');
        } else {
          return;
        }
        that.ajax({
          url: '/cart/product/count?memberId=' + memberId,
          success: function (res) {
            that.shopCount = res.data;
          }
        })
      },
    },
    created() {
      this.getInfo();
      this.getShopCount();
      document.documentElement.scrollTop = 0;
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  .detail-main {
    padding: $top 0 $bottom;

  }

  .detail-main-image {
    width: 100%;
    height: 50vh;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .detail-main-title {
    @include fj;
    flex-direction: row;
    padding: 15px;
    background: $f;
    .detail-title {
      @include sh($med, 1.5rem);
      @include el(2);
      height: 3rem;
      flex: 1;
    }
    .detail-collect {
      border-left: 1px solid $border;
      @include sc($min, $gray);
      padding-left: 10px;
      text-align: center;
      .iconfont {
        display: block;
        @include wh(2rem, 2rem);
        background: url('../../assets/img/heart_03.png') no-repeat left -32px;
        background-size: cover;
      }
      .active {
        background-position: left 0px;
      }
    }
  }

  .detail-main-money {
    padding: 0 15px;
    @include sc($med, $red);
    background: $f;
  }

  .detail-main-mon {
    padding: 5px 15px;
    background: $f;
    @include sc($med, $grey);
    span {
      text-decoration: line-through;
      margin-left: 10px;
    }
  }

  .detail-main-sales {
    background: $f;
    padding: 0 15px 15px;
    @include fj;
    @include sc($min, $grey);

  }

  .detail-main-intro {
    margin-bottom: 10px;
    background: $f;
    .html-detail {
      padding: 10px;
    }
  }

  .detail-main-tag {
    padding: 15px;
    @include sc($min, $gray);
    .detail-notice {
      padding-bottom: $min;
    }
    .detail-tag-list {
      float: left;
      margin-right: $med;
      .iconfont {
        background: $red;
        color: $f;
        border-radius: 100%;
        padding: 2px;
        margin-right: 4px;
      }
    }

  }

  .detail-main-sel {
    background: $f;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    position: relative;
    &:after {
      @include mid;
      right: 15px;
      display: block;
      content: '';
      @include wh(0.6rem, 0.6rem);
      transform-origin: 0.5rem -0.5rem;
      transform: rotateZ(45deg);
      border-right: 1px solid $grey;
      border-top: 1px solid $grey;
    }
    .detail-sel-left {
      padding: 15px;

    }
    .detail-sel-evaluate {
      padding: 15px;
      padding-left: 0;
      span {
        color: $red;
      }
    }
  }

  //选择弹窗
  .sel-info {
    @include wh(100vw, 60vh);
    background: $f;
    .sel-info-title {
      padding: 15px;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      border-bottom: 1px solid $border;
      position: relative;
      z-index: 999;
      .sel-info-image {
        @include wh(6rem, 6rem);
        img {
          @include wh(6rem, 6rem);
        }
      }
      .sel-info-item {
        padding-left: 10px;
        flex: 1;
        @include sh($med, 1.5rem);
        .sel-info-name {
          @include sh($med, 1.5rem);
          @include el(2);
          height: 3rem;
          .iconfont {
            float: right;
            @include sh(1.6rem, 1em);
          }
        }
      }
    }
    .sel-info-sel {
      padding: 15px 15px 0;
      overflow-y: scroll;
      position: absolute;
      top: 8rem;
      bottom: $med * 4;
      .sel-title {

      }
      .sel-list {
        padding: 10px 0;
        .sel-li {
          float: left;
          border: 1px solid $border;
          border-radius: 5px;
          padding: 5px 10px;
          min-width: 2rem;
          min-height: 2rem;
          margin-right: 10px;
          margin-bottom:10px;
        }
        .activeClass {
          border: 1px solid $red;
          color: $red;
        }
      }
    }
    .sel-info-button {
      @include fj;
      padding: 10px;
      background: $border;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      .sel-info-btn {
        flex: 1;
        text-align: center;
        border-radius: 5px;
        @include sh($med, 2.6em);
        color: $f;

      }

    }
    .sel-count-title {
      padding-bottom: 10px;
    }
    .sel-minus-add {
      display: flex;
      flex-direction: row;
      @include sh($med, 2em);
      text-align: center;
      .sel-minus {
        width: 2.2rem;
        border: 1px solid $border;
      }
      .sel-count {
        border-top: 1px solid $border;
        border-bottom: 1px solid $border;
        min-width: 3.4rem;
      }
      .sel-add {
        border: 1px solid $border;
        width: 2.2rem;
      }
    }
  }

  /*公共样式*/
  .sel-info-addShop {
    background: rgb(244, 162, 19);
    margin-right: 10px;
  }

  .sel-info-buy {
    background: $red;
  }

  .orderDetail-button {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 10px;
    @include fj;
    flex-direction: row;
    border-top: 1px solid $border;
    background: $border;
    .orderDetail-shopcart {
      margin-right: 10px;
      display: flex;
      flex-direction: column;
      @include sh(0.6rem, 1em);
      position: relative;
      .iconfont {
        @include sh($min *2, 1em);
        text-align: center;
      }
      .superscript {
        position: absolute;
        display: block;
        @include wh(1rem, 1rem);
        top: 0;
        right: 0;
        background: red;
        color: $f;
        text-align: center;
        border-radius: 100%;
        line-height: 1rem;
      }
    }
    .orderDetail-btn {
      flex: 1;
      text-align: center;
      border-radius: 5px;
      @include sh($med, 2.6em);
      color: $f;
    }

  }
</style>



